<template>
  <div>
    <v-row v-if="loggedAs === 'school'">
      <v-col cols="12">
        <v-btn color="primary" @click="showCreateStudentDialog = true">
          New Student
        </v-btn>
        <v-btn
          class="ml-5"
          color="primary"
          :disabled="selectedStudents.length < 1"
          @click="openEnrollDialog()"
        >
          Enroll Student
        </v-btn>
      </v-col>
    </v-row>

    <v-card outlined raised>
      <v-card-title>
        <v-spacer></v-spacer>
        <v-text-field
          v-model="studentSearch"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="students"
        :search="studentSearch"
        show-select
        @update:page="pageChanged"
        @click:row="goToStudentProfile"
        v-model="selectedStudents"
      >
        <template v-slot:item.action="{ item }">
          <v-icon @click="deleteItem(item)" color="error">
            mdi-delete
          </v-icon>
        </template>
      </v-data-table>
    </v-card>

    <v-dialog v-model="showEnrollDialog" scrollable max-width="60vw">
      <v-card>
        <v-card-title>
          <v-spacer></v-spacer>
          <v-text-field
            v-model="offerSearch"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-data-table
          :headers="offersHeaders"
          :items="offers"
          :search="offerSearch"
          show-select
          v-model="selectedOffers"
          class="elevation-1"
        >
          <template v-slot:item.action="{ item }">
            <v-icon @click="deleteItem(item)" color="error">
              mdi-delete
            </v-icon>
          </template>
        </v-data-table>
        <v-card-actions>
          <v-btn color="success" @click="enrollStudent">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="showCreateStudentDialog" max-width="60vw">
      <v-card>
        <v-card-title>
          <v-row>
            <v-col cols="6">
              <v-col cols="12">
                <v-textarea
                  rows="1"
                  label="First Name"
                  v-model="firstName"
                  outlined
                  auto-grow
                  full-width
                >
                </v-textarea>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  rows="1"
                  label="Middle Name"
                  v-model="middleName"
                  outlined
                  auto-grow
                  full-width
                >
                </v-textarea>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  rows="1"
                  label="Last Name"
                  v-model="lastName"
                  outlined
                  auto-grow
                  full-width
                >
                </v-textarea>
              </v-col>
            </v-col>
            <v-col cols="6">
              <v-card raised>
                <v-date-picker
                  v-model="birthday"
                  full-width
                  raised
                  scrollable
                  header-color="green"
                ></v-date-picker>
              </v-card>
            </v-col>
          </v-row>
        </v-card-title>
        <v-card-actions>
          <v-spacer> </v-spacer>
          <v-btn
            color="success"
            @click="createStudent"
            :disabled="firstName.length < 2 || lastName.length < 2"
            >Create</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import OfferService from "../services/OfferService";
import StudentService from "../services/StudentService";
import CourseService from "../services/CourseService";
import TeacherService from "../services/TeacherService";

export default {
  data() {
    return {
      students: [],
      selectedStudents: [],
      selectedOffers: [],
      offers: [],
      teachers: [],
      courses: [],
      headers: [
        {
          text: "First Name",
          align: "start",
          value: "firstName"
        },
        {
          text: "Middle Name",
          align: "start",
          value: "middleName"
        },
        {
          text: "Last Name",
          align: "start",
          value: "lastName"
        },
        { text: "Enrolled Count", value: "enrolledCount" },
        { text: "Actions", value: "action", sortable: false }
      ],
      offersHeaders: [
        {
          text: "Course",
          align: "start",
          value: "course"
        },
        {
          text: "Teacher",
          value: "teacher"
        }
      ],
      firstName: "",
      middleName: "",
      lastName: "",
      birthday: new Date().toISOString().substr(0, 10),
      offerSearch: "",
      studentSearch: "",
      showCreateStudentDialog: false,
      showEnrollDialog: false
    };
  },
  created() {
    StudentService.GetStudents().then(students => {
      this.students = students;
      //   students.map(el => (el.average = 0));
      //   let assignmentPromises = [];
      //   students.forEach(student => {
      //     assignmentPromises.push(
      //       StudentService.GetStudentAssignments(student.id).then(
      //         assignments => {
      //           const average = StudentService.CalculateAssignmentsAverage(
      //             assignments
      //           );
      //           this.$set(student, "average", Math.round(average));
      //         }
      //       )
      //     );
      //   });
      //   return Promise.all(assignmentPromises);
      // })
      // .then(() => {
      //   this.students.sort((a, b) => {
      //     return b.average - a.average;
      //   });

      //   for (let index = 0; index < this.students.length; index++) {
      //     this.$set(this.students[index], "ranking", index + 1);
      //   }
    });
  },
  methods: {
    deleteItem(item) {
      const index = this.students.indexOf(item);
      const isConfirmed = confirm("Are you sure you want to delete this item?");
      if (isConfirmed) {
        StudentService.DeleteStudent(item.id).then(() => {
          this.students.splice(index, 1);
        });
      }
    },

    createStudent() {
      StudentService.CreateStudent({
        firstName: this.firstName,
        middleName: this.middleName,
        lastName: this.lastName,
        birthday: this.birthday
      }).then(() => {
        this.$router.go();
      });
    },

    openEnrollDialog() {
      this.showEnrollDialog = true;

      if (this.offers.length == 0) {
        OfferService.GetOffers(1).then(offers => {
          this.offers = offers;

          for (let i = 0; i < offers.length; i++) {
            const el = offers[i];
            TeacherService.GetTeacherById(el.teacherId).then(teacher => {
              el.teacher = teacher.name;
              this.offers.splice(i, 1, el);
            });
            CourseService.GetCourseById(el.courseId).then(course => {
              el.course = course.name;
              this.offers.splice(i, 1, el);
            });
          }
        });
      }
    },

    enrollStudent() {
      const studentIds = this.selectedStudents.map(x => x.id);
      const offerIds = this.selectedOffers.map(x => x.id);
      StudentService.EnrollStudent(studentIds, offerIds).then(() => {
        this.$router.go();
      });
    },

    goToStudentProfile(clickedRow) {
      this.$router.push({
        name: "ViewStudent",
        params: { studentId: clickedRow.id, ranking: clickedRow.ranking }
      });
    },

    pageChanged(index) {
      console.log(index);
    }
  },

  computed: {
    loggedAs() {
      return this.$store.state.loggedAs;
    }
  }
};
</script>

